import React, {useState, useRef} from 'react'
import {useDispatch} from 'react-redux'
import './Form.css'

export default function Form() {

    // const [article, setArticle] = useState({
    //     title: "",
    //     body: ""

    // })

    const dispatch = useDispatch();

    const handleForm = e => {
        e.preventDefault();

    const newArticle = {
        title: inputsRef.current[0].value,
        body: inputsRef.current[1].value,

    }




        // Envoyez l'article.
        dispatch({
            type:'ADDARTICLE',
            //payload: article
            payload: newArticle
        }); 
        
        e.target.reset()
        // Mettre à zéro.
    //     setArticle({
    //         title: "",
    //         body: ""
    //     })
    };

    // const handleInputs = e => {
        
    //     if(e.target.classList.contains('inp-title')){
    //         const newObjState = {...article, title: e.target.value};
    //         setArticle(newObjState);
    //     } 
    //     else if(e.target.classList.contains('inp-body')) {
    //         const newObjState = {...article, body: e.target.value};
    //         setArticle(newObjState);
    //     }

    //};
    // console.log(article);

    const inputsRef = useRef([]);

    const addRefs = (el) => {
        //s' il est présent et qu'il n'est pas déjà mis dans le tableau alors
        if (el && !inputsRef.current.includes(el)) {
            // Alors on lui met cet élément.
            inputsRef.current.push(el);
        }

    };


    return (
        <>
                                      {/* alt 144 pour écrive un e maj   */}
           <h1 className="title-form">Écriver un article</h1> 

           <form onSubmit={handleForm}
            className="container-form">
                <label htmlFor="title">Titre</label>
                <input
                ref= {addRefs}
                // onChange={handleInputs}
                // value={article.title}
               // On peut utiliser onChange ou inInput, c'est la même chose.
                 type="text"
                  id="title"
                   placeholder="Entrez votre nom" 
                   className="inp-title"/>
                   

                <label htmlFor="article">Votre article</label>
                <textarea
                ref= {addRefs}
                // onChange={handleInputs}
                // value={article.body}
                  id="article"
                   placeholder="Votre article"
                   className="inp-body">
                   </textarea>

                <button>Envoyez l'article</button>

           </form>
        </>
    )
}
